import {mapGetters} from 'vuex'

export const playlistMixin = {
  computed: {
    ...mapGetters([
      'playlist'
    ])
  },
  mounted() {
    this.adjustPlaylist(this.playlist)
  },
  activated() {
    this.adjustPlaylist(this.playlist)
  },
  watch: {
    playlist() {
      this.adjustPlaylist(this.playlist)
    }
  },
  methods: {
    adjustPlaylist(playlist){ //  playlist  bottom scroll 
      throw Error('adjustPlaylist has not defined')
    }
  }
}